import { AfterViewInit, Component, HostBinding, ViewChild } from '@angular/core';
import { Content, InfiniteScroll, NavParams, Searchbar, ViewController } from 'ionic-angular';
import { IonicSelectableComponent } from './ionic-selectable.component';

@Component({
  selector: 'ionic-selectable-page',
  template: `<ion-header>
  <ion-navbar [color]="selectComponent.headerColor ? selectComponent.headerColor : null">
    <ion-title>
      <!-- Use span for titleTemplate to preserve Ionic ellipsis styles. -->
      <span *ngIf="selectComponent.titleTemplate" [ngTemplateOutlet]="selectComponent.titleTemplate">
      </span>
      <span *ngIf="!selectComponent.titleTemplate">
        {{selectComponent._labelText}}
      </span>
    </ion-title>
    <ion-buttons start>
      <button ion-button (click)="_close()">
        <span *ngIf="selectComponent.closeButtonTemplate" [ngTemplateOutlet]="selectComponent.closeButtonTemplate">
        </span>
        <span *ngIf="!selectComponent.closeButtonTemplate" ion-text showWhen="ios">
          {{selectComponent.closeButtonText}}
        </span>
        <ion-icon *ngIf="!selectComponent.closeButtonTemplate" name="md-close"
          hideWhen="ios">
        </ion-icon>
      </button>
    </ion-buttons>
  </ion-navbar>
  <ion-toolbar *ngIf="selectComponent.canSearch || selectComponent.messageTemplate">
    <ion-searchbar *ngIf="selectComponent.canSearch" #searchbarComponent
      [(ngModel)]="selectComponent._searchText" (ionInput)="selectComponent._filterItems()"
      [placeholder]="selectComponent.searchPlaceholder" [debounce]="selectComponent.searchDebounce">
    </ion-searchbar>
    <div class="ionic-selectable-message" *ngIf="selectComponent.messageTemplate">
      <div [ngTemplateOutlet]="selectComponent.messageTemplate">
      </div>
    </div>
  </ion-toolbar>
</ion-header>
<ion-content>
  <div class="ionic-selectable-spinner" *ngIf="selectComponent._isSearching">
    <div class="ionic-selectable-spinner-background"></div>
    <ion-spinner></ion-spinner>
  </div>
  <ion-list no-margin *ngIf="!selectComponent.hasVirtualScroll && selectComponent._filteredGroups.length">
    <ion-item-group *ngFor="let group of selectComponent._filteredGroups" class="ionic-selectable-group">
      <ion-item-divider *ngIf="selectComponent._hasGroups" [color]="selectComponent.groupColor ? selectComponent.groupColor : null">
        <!-- Use span for groupTemplate to preserve Ionic ellipsis styles. -->
        <span *ngIf="selectComponent.groupTemplate" [ngTemplateOutlet]="selectComponent.groupTemplate"
          [ngTemplateOutletContext]="{ group: group }">
        </span>
        <span *ngIf="!selectComponent.groupTemplate">
          {{group.text}}
        </span>
        <div *ngIf="selectComponent.groupRightTemplate" item-right>
          <div [ngTemplateOutlet]="selectComponent.groupRightTemplate"
            [ngTemplateOutletContext]="{ group: group }">
          </div>
        </div>
      </ion-item-divider>
      <button ion-item detail-none *ngFor="let item of group.items" (click)="_select(item)"
        class="ionic-selectable-item" [ngClass]="{
                    'ionic-selectable-item-is-selected': selectComponent._isItemSelected(item),
                    'ionic-selectable-item-is-disabled': selectComponent._isItemDisabled(item)
                }"
        [disabled]="selectComponent._isItemDisabled(item)">
        <ion-icon [name]="selectComponent._isItemSelected(item) ? 'checkmark-circle' : 'radio-button-off'"
          [color]="selectComponent._isItemSelected(item) ? 'primary' : 'daek'"
          item-left>
        </ion-icon>
        <!-- Use span for itemTemplate to preserve Ionic ellipsis styles. -->
        <span *ngIf="selectComponent.itemTemplate" [ngTemplateOutlet]="selectComponent.itemTemplate"
          [ngTemplateOutletContext]="{ item: item }">
        </span>
        <span *ngIf="!selectComponent.itemTemplate">
          {{selectComponent._formatItem(item)}}
        </span>
        <div *ngIf="selectComponent.itemRightTemplate" item-right>
          <div [ngTemplateOutlet]="selectComponent.itemRightTemplate"
            [ngTemplateOutletContext]="{ item: item }">
          </div>
        </div>
        <button *ngIf="selectComponent.canSaveItem" class="ionic-selectable-item-button"
          ion-button icon-only item-end outline (click)="selectComponent._saveItem($event, item)">
          <ion-icon name="md-create"></ion-icon>
        </button>
        <button *ngIf="selectComponent.canDeleteItem" class="ionic-selectable-item-button"
          ion-button icon-only item-end outline (click)="selectComponent._deleteItemClick($event, item)">
          <ion-icon name="md-trash"></ion-icon>
        </button>
      </button>
    </ion-item-group>
  </ion-list>
  <ion-infinite-scroll *ngIf="!selectComponent.hasVirtualScroll" [enabled]="selectComponent.hasInfiniteScroll"
    (ionInfinite)="_getMoreItems($event)">
    <ion-infinite-scroll-content></ion-infinite-scroll-content>
  </ion-infinite-scroll>
  <ion-list no-margin *ngIf="selectComponent.hasVirtualScroll && selectComponent._filteredGroups.length"
    [virtualScroll]="selectComponent._filteredGroups[0].items"
    [approxItemHeight]="selectComponent.virtualScrollApproxItemHeight"
    [approxItemWidth]="selectComponent.virtualScrollApproxItemWidth"
    [bufferRatio]="selectComponent.virtualScrollBufferRatio" [headerFn]="selectComponent.virtualScrollHeaderFn">
    <ion-item-divider *virtualHeader="let header" [color]="selectComponent.groupColor ? selectComponent.groupColor : null">
      {{header}}
    </ion-item-divider>
    <button ion-item detail-none *virtualItem="let item" (click)="_select(item)"
      class="ionic-selectable-item" [ngClass]="{
                'ionic-selectable-item-is-selected': selectComponent._isItemSelected(item),
                'ionic-selectable-item-is-disabled': selectComponent._isItemDisabled(item)
            }"
      [disabled]="selectComponent._isItemDisabled(item)">
      <ion-icon [name]="selectComponent._isItemSelected(item) ? 'checkmark-circle' : 'radio-button-off'"
        [color]="selectComponent._isItemSelected(item) ? 'primary' : 'daek'"
        item-left>
      </ion-icon>
      <!-- Use span for itemTemplate to preserve Ionic ellipsis styles. -->
      <span *ngIf="selectComponent.itemTemplate" [ngTemplateOutlet]="selectComponent.itemTemplate"
        [ngTemplateOutletContext]="{ item: item }">
      </span>
      <span *ngIf="!selectComponent.itemTemplate">
        {{selectComponent._formatItem(item)}}
      </span>
      <div *ngIf="selectComponent.itemRightTemplate" item-right>
        <div [ngTemplateOutlet]="selectComponent.itemRightTemplate"
          [ngTemplateOutletContext]="{ item: item }">
        </div>
      </div>
      <button *ngIf="selectComponent.canSaveItem" class="ionic-selectable-item-button"
        ion-button icon-only item-end outline (click)="selectComponent._saveItem($event, item)">
        <ion-icon name="md-create"></ion-icon>
      </button>
      <button *ngIf="selectComponent.canDeleteItem" class="ionic-selectable-item-button"
        ion-button icon-only item-end outline (click)="selectComponent._deleteItemClick($event, item)">
        <ion-icon name="md-trash"></ion-icon>
      </button>
    </button>
  </ion-list>
  <div *ngIf="!selectComponent._filteredGroups.length || !selectComponent._filteredGroups[0].items.length">
    <span *ngIf="selectComponent.searchFailTemplate" [ngTemplateOutlet]="selectComponent.searchFailTemplate">
    </span>
    <div *ngIf="!selectComponent.searchFailTemplate" margin>
      {{selectComponent.searchFailText}}
    </div>
  </div>
  <div class="ionic-selectable-add-item-template" *ngIf="selectComponent._isAddItemTemplateVisible"
    [ngStyle]="{ 'top.px': _content.contentTop }">
    <span [ngTemplateOutlet]="selectComponent.addItemTemplate"
      [ngTemplateOutletContext]="{ item: selectComponent._itemToAdd, isAdd: selectComponent._itemToAdd === null }">
    </span>
  </div>
</ion-content>
<ion-footer *ngIf="selectComponent._footerButtonsCount > 0" [ngStyle]="{ 'visibility': selectComponent._isFooterVisible ? 'initial' : 'hidden' }">
  <ion-toolbar>
    <ion-row>
      <ion-col *ngIf="selectComponent.canClear" [attr.col-4]="selectComponent._footerButtonsCount === 3 ? '' : null"
        [attr.col-6]="selectComponent._footerButtonsCount === 2 ? '' : null"
        [attr.col-12]="selectComponent._footerButtonsCount === 1 ? '' : null">
        <button ion-button full no-margin (click)="_clear()" [disabled]="!selectComponent._selectedItems.length">
          {{selectComponent.clearButtonText}}
        </button>
      </ion-col>
      <ion-col *ngIf="selectComponent.canAddItem" [attr.col-4]="selectComponent._footerButtonsCount === 3 ? '' : null"
        [attr.col-6]="selectComponent._footerButtonsCount === 2 ? '' : null"
        [attr.col-12]="selectComponent._footerButtonsCount === 1 ? '' : null">
        <button ion-button full no-margin (click)="selectComponent._addItemClick()">
          {{selectComponent.addButtonText}}
        </button>
      </ion-col>
      <ion-col *ngIf="selectComponent.isMultiple" [attr.col-4]="selectComponent._footerButtonsCount === 3 ? '' : null"
        [attr.col-6]="selectComponent._footerButtonsCount === 2 ? '' : null"
        [attr.col-12]="selectComponent._footerButtonsCount === 1 ? '' : null">
        <button ion-button full no-margin (click)="_ok()" [disabled]="!selectComponent.isOkButtonEnabled">
          {{selectComponent.okButtonText}}
        </button>
      </ion-col>
    </ion-row>
  </ion-toolbar>
</ion-footer>
`
})
export class IonicSelectablePageComponent implements AfterViewInit {
  @HostBinding('class.ionic-selectable-page')
  private _cssClass = true;
  @HostBinding('class.ionic-selectable-page-can-clear')
  private get _canClearCssClass(): boolean {
    return this.selectComponent.canClear;
  }
  @HostBinding('class.ionic-selectable-page-is-multiple')
  private get _isMultipleCssClass(): boolean {
    return this.selectComponent.isMultiple;
  }
  @HostBinding('class.ionic-selectable-page-is-searching')
  private get _isSearchingCssClass(): boolean {
    return this.selectComponent._isSearching;
  }
  @HostBinding('class.ionic-selectable-page-is-add-item-template-visible')
  private get _isAddItemTemplateVisibleCssClass(): boolean {
    return this.selectComponent._isAddItemTemplateVisible;
  }
  @HostBinding('class.ionic-selectable-page-ios')
  private get _isIos(): boolean {
    return this.selectComponent._isIos;
  }
  @HostBinding('class.ionic-selectable-page-md')
  private _isMD(): boolean {
    return this.selectComponent._isMD;
  }
  @ViewChild('searchbarComponent')
  private _searchbarComponent: Searchbar;
  @ViewChild(Content)
  _content: Content;
  selectComponent: IonicSelectableComponent;

  constructor(
    private navParams: NavParams,
    private viewController: ViewController
  ) {
    this.selectComponent = this.navParams.get('selectComponent');
    this.selectComponent._selectPageComponent = this;
    this.selectComponent._selectedItems = [];

    if (!this.selectComponent._isNullOrWhiteSpace(this.selectComponent.value)) {
      if (this.selectComponent.isMultiple) {
        this.selectComponent.value.forEach(item => {
          this.selectComponent._selectedItems.push(item);
        });
      } else {
        this.selectComponent._selectedItems.push(this.selectComponent.value);
      }
    }

    this._setItemsToConfirm(this.selectComponent._selectedItems);
  }

  ngAfterViewInit() {
    if (this._searchbarComponent && this.selectComponent.focusSearchbar) {
      // Focus after a delay because focus doesn't work without it.
      setTimeout(() => {
        this._searchbarComponent.setFocus();
      }, 1000);
    }
  }

  private _setItemsToConfirm(items: any[]) {
    // Return a copy of original array, so it couldn't be changed from outside.
    this.selectComponent._itemsToConfirm = [].concat(items);
  }

  _getMoreItems(infiniteScroll: InfiniteScroll) {
    // TODO: Try to get infiniteScroll via ViewChild. Maybe it works in a newer Ionic version.
    // For now assign it here.
    this.selectComponent._infiniteScroll = infiniteScroll;

    this.selectComponent.onInfiniteScroll.emit({
      component: this.selectComponent,
      text: this.selectComponent._searchText
    });
  }

  _select(item: any) {
    if (this.selectComponent.isMultiple) {
      if (this.selectComponent._isItemSelected(item)) {
        this.selectComponent._deleteSelectedItem(item);
      } else {
        this.selectComponent._addSelectedItem(item);
      }

      this._setItemsToConfirm(this.selectComponent._selectedItems);
    } else {
      if (!this.selectComponent._isItemSelected(item)) {
        this.selectComponent._selectedItems = [];
        this.selectComponent._addSelectedItem(item);

        if (this.selectComponent._shouldStoreItemValue) {
          this.selectComponent._select(this.selectComponent._getItemValue(item));
        } else {
          this.selectComponent._select(item);
        }
      }

      this._close();
    }
  }

  _ok() {
    this.selectComponent._select(this.selectComponent._selectedItems);
    this._close();
  }

  _close() {
    // Focused input interferes with the animation.
    // Blur it first, wait a bit and then close the page.
    if (this._searchbarComponent) {
      this._searchbarComponent._fireBlur();
    }

    setTimeout(() => {
      this.selectComponent.close().then(() => {
        this.selectComponent.onClose.emit({
          component: this.selectComponent
        });
      });

      if (!this.selectComponent._hasOnSearch()) {
        this.selectComponent._searchText = '';
        this.selectComponent._setHasSearchText();
      }
    });
  }

  _clear() {
    this.selectComponent.clear();
    this.selectComponent._emitValueChange();
    this.selectComponent.close().then(() => {
      this.selectComponent.onClose.emit({
        component: this.selectComponent
      });
    });
  }
}
